export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './colors.html';\n\nexport default class ExampleEmptyColors extends Component {\n  static template = _tpl;\n}\n","sass":".demo {\n  :global {\n    div {\n      text-align: center;\n    }\n\n    .md-empty-state {\n      display: inline-block;\n      vertical-align: middle;\n\n      + .md-empty-state {\n        margin-left: 16px;\n      }\n    }\n  }\n}\n","html":"<!-- import style from './colors.scss' -->\n<div :class=\"style.demo\">\n  <div>\n    <md-empty-state\n      class=\"md-primary\"\n      label=\"Nothing in Done\"\n      description=\"Anything you mark done will be safely stored here.\"\n    >\n      <md-icon-done slot-pass:icon/>\n    </md-empty-state>\n\n    <md-empty-state\n      class=\"md-accent\"\n      rounded\n      label=\"Nothing in Reminders\"\n      description=\"Create a Reminder and it will show up here.\"\n    >\n      <md-icon-alarm_off slot-pass:icon/>\n    </md-empty-state>\n  </div>\n</div>"}